/* General Styles */
.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .chart-card {
    margin-bottom: 20px;
  }
  
  /* Dropdown */
  .select-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
  }
  
  /* Tabs Styling */
  .tabs-list {
    display: flex;
    gap: 12px;
  }
  
  .tab-trigger {
    padding: 10px 20px;
    font-weight: 600;
    cursor: pointer;
  }
  
  .tab-trigger[data-state='active'] {
    background-color: #2563eb;
    color: white;
  }
  